import jss from 'jss';
import jssGlobal from 'jss-plugin-global';
import jssNested from 'jss-plugin-nested';

/**
 * 设置textarea字体大小
 * @param baseCls 基础类
 * @param fontSize 字体大小
 */
export function setTextAreaFontSize(baseCls: string, fontSize: number): string {
    const cls = `${baseCls}-${BI.UUID()}`;

    const selector = `.${cls}`;

    jss.use(jssGlobal(), jssNested());

    const baseSelector = `${selector}.bi-textarea-editor`;

    jss.createStyleSheet({
        '@global': {
            [`${baseSelector} .textarea-editor-content`]: {
                'font-size': `${fontSize}px`,
                'line-height': `${fontSize * 1.75}px`,
            },
            [`${baseSelector} .bi-water-mark`]: {
                'font-size': `${fontSize}px`,
                'line-height': `${fontSize * 1.75}px`,
            },
        },
    }).attach();

    return cls;
}
